#home
{
    header 
    {
        position: absolute;
        font-family: 'Nunito';
        margin-bottom: 5vh;

        h1 {
            padding: 0;
            margin: 0;
            color: #000;
            font-family: Nunito;
            font-size: 5.25vw;
            font-style: normal;
            font-weight: 500;
            line-height: 12.8vh;
            letter-spacing: -2.6px;
        }

        h2 
        {
            padding: 0;
            margin :0;
            text-align: center;
            color: var(--theme-color-0);
            font-family: Nunito;
            font-size: 2vw;
            margin-top: 4vh;
            font-style: normal;
            font-weight: 400;
            line-height: 0px;
        }
    }
    
}

@media screen and (min-width: 1920px) 
{
    #home header 
    {
        h1 
        {
            font-size: 100px;
        }
        h2
        {
            font-size: 35px;
        }
    }
}
@media  screen and (max-width: 1300px) 
{
}
@media screen and (min-height: 1080px) 
{
    #home header {
        h1 
        {
            line-height: 128px;
        }
        h2
        {
            margin-top: 40px;
        }
    }
}