nav 
{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100px;
    display: flex;
    justify-content: center;
    z-index: 4;
}
nav .text-logo
{
    position: absolute;
    left: 50px;
    top: 40px;
    font-size: 25px;
    font-family: "Lato";
    font-weight: 700;
    color: var(--menu-text-logo-color);
    transition: color 0.5s;
}
nav .menu
{
    position: absolute;
    top: 40px;
    width: fit-content;
    height: 50px;
    border-radius: 50px;
    background-color: var(--menu-background-color);
    transition: background-color 0.3s;
}
nav .menu .menu-btns
{
    position: relative;
    width: fit-content;
    height: 100%;
    display: flex;
    flex-direction: row;
    gap: 5px;
}
nav .menu .selected-btn-bg
{
    position: absolute;
    left: 0;
    height: 100%;
    width: 105px;
    border-radius: 50px;
    background-color: var(--menu-color);
    transition: 0.3s;
}
nav .menu .menu-btns .menu-btn,
nav .menu .menu-btns .selected-btn
{
    display: flex;
    align-items: center;
    padding: 0 25px;
    align-self: stretch;
    cursor: pointer;
    color: #c9c9c9;
    font-family: "Lato";
    letter-spacing: 1px;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-transform: capitalize;
    border-radius: 50px;
    transition: 0.3s;
}
nav .menu .menu-btns .selected-btn
{
    color: white;
}

nav .menu .menu-btns .menu-btn:hover 
{
    color: var(--menu-button-hover-color);
}